.cyclic-anim {
  position: relative;
  display: block;
  height: 35px;
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.cyclic-anim li {
  position: absolute;
  top: 60px;
  left: 0;
  padding: 0;
  transition: all 500ms;
  line-height: 1;
  opacity: 0;
  width: 100%;
  text-align: center;
}
#start .cyclic-anim li:before {
  display: none;
}
.cyclic-anim li.active {
  top: 0;
  opacity: 1;
}
.cyclic-anim li.prev {
  top: -40px;
}
